export const addMonths = (date, months) => {
    date.setMonth(date.getMonth() + months);
    return date;
}

export const currency_type_display = (currency_type) => {
    if (currency_type === "Million") {
        return "m"
    }
    else {
        return "k"
    }
}

export const truncate = (num) => {
    return parseFloat(num.toString().match(/^-?\d+(?:\.\d{0,2})?/)[0])
}