import React from 'react'

import ReactApexChart from 'react-apexcharts'
import ApexCharts from 'apexcharts';
import { currency_type_display, truncate } from "../../utils/_funcs"
import SpinnerComponent from '../../utils/spinner';

const mapData = (data, dashboard_metric, currency_type) => {

    let values = []
    for (let i = 0; i < data.length; i++) {

        values.push(
            [
                new Date(data[i].date).getTime(),
                currency_type === "Million" ? truncate((dashboard_metric ? data[i].sum : data[i].quantity) / 1000000) : truncate((dashboard_metric ? data[i].sum : data[i].quantity) / 1000)
            ]
        )
    }

    return values
}

class DepotProductSeriesChart extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            spinner: false,
            series: [],
            currency_type: this.props.currency_type,
            selection_dates: this.props.selection_dates,
            options: {
                chart: {
                    id: 'area-datetime',
                    type: 'area',
                    height: 400,
                    zoom: {
                        autoScaleYaxis: false
                    }
                },
                title: {
                    text: this.props.dashboard_metric ? "Revenue" : "Quantities"
                },
                annotations: {

                    xaxis: [{
                        x: new Date(`${this.props.selection_dates.startDate}`).getTime(),
                        borderColor: '#999',
                        yAxisIndex: 0,

                    }]
                },
                dataLabels: {
                    enabled: true
                },
                markers: {
                    size: 0,
                    style: 'hollow',
                },
                xaxis: {
                    type: 'datetime',
                    // min: new Date(`${this.props.selection_dates.startDate}`).getTime(),
                    // max: new Date(`${this.props.selection_dates.endDate}`).getTime(),
                    tickAmount: 5,
                },
                yaxis: {
                    title: {
                        text: 'Kshs (Millions)'
                    },
                    min: 0,
                },
                tooltip: {
                    x: {
                        format: 'dd MMM yyyy'
                    },
                    y: {
                        formatter: this.props.dashboard_metric ? function (val) {
                            return `Kshs ${val} Millions`
                        } : function (val) {
                            return `${val} Litres`
                        }
                    }
                },
                // fill: {
                //   type: 'gradient',
                //   gradient: {
                //     shadeIntensity: 1,
                //     opacityFrom: 0.7,
                //     opacityTo: 0.9,
                //     stops: [0, 100]
                //   }
                // },
            },


            selection: 'one_month',

        };
    }

    static getDerivedStateFromProps = (props, state) => {
        const { depot_product_series, update_product_series, selection_dates, currency_type, dashboard_metric, update_depot_series } = props
        if (update_depot_series || update_product_series) {
            state.spinner = true
            return state
        } else {
            state.spinner = false
        }
        let data = depot_product_series

        data = data.revenue
        if (typeof (selection_dates.startDate) === "string") {
            selection_dates.startDate = new Date(`${selection_dates.startDate}`)
            selection_dates.endDate = new Date(`${selection_dates.endDate}`)
        }
        let series = []

        for (let i = 0; i < data.length; i++) {
            let prod = data[i].data
            let d = mapData(prod, dashboard_metric, currency_type)

            series = series.concat({
                name: data[i].name,
                data: d
            })
        }
        state.series = series
        state.options.title.text = dashboard_metric ? "Revenue" : "Quantities"
        // state.options.xaxis.min = selection_dates.startDate.getTime()
        // state.options.xaxis.max = selection_dates.endDate.getTime()
        state.options.yaxis.title.text = dashboard_metric ? `Kshs (${currency_type})` : `Litres (${currency_type})`
        state.options.tooltip.y.formatter = dashboard_metric ? function (val) {
            return `Kshs ${val}${currency_type_display(currency_type)}`
        } : function (val) {
            return `${val}${currency_type_display(currency_type)} Litres`
        }
        state.selection_dates = selection_dates
        state.currency_type = currency_type
        return state
    }

    render() {
        try {

            ApexCharts.exec(
                'area-datetime',
                'zoomX',
                new Date(`${this.state.selection_dates.startDate}`).getTime(),
                new Date(`${this.state.selection_dates.endDate}`).getTime(),

            )
        } catch (e) {
            "in error"
        }
        return (
            <div id="chart" >

                {this.state.spinner ? <div style={{ height: 400, position: "relative" }}><SpinnerComponent spinner={this.state.spinner} /></div> :
                    <div id="chart-timeline">
                        <ReactApexChart options={this.state.options} series={this.state.series} type="area" height={400} />
                    </div>
                }
            </div>


        );
    }
}

export default DepotProductSeriesChart